import { useEffect, useState } from "react";
import { nowPlaying, popular, topRated, upComing } from "../../api";
import { MainBanner } from "./MainBanner";
import { Loading } from "../../components/Loading";
import "swiper/css";
import { Movies } from "./Movies";
import { PageTitle } from "../../components/PageTitle";

export const Home = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [nowData, setNowData] = useState();
  const [popData, setPopData] = useState();
  const [topData, setTopData] = useState();
  const [upData, setUpData] = useState();

  useEffect(() => {
    (async () => {
      try {
        const { results: nowResult } = await nowPlaying();
        const { results: popResult } = await popular();
        const { results: topResult } = await topRated();
        const { results: upResult } = await upComing();

        setNowData(nowResult);
        setPopData(popResult);
        setTopData(topResult);
        setUpData(upResult);
        setIsLoading(false);
      } catch (error) {
        console.log(error);
        alert(" ");
      }
    })();
  }, []);

  return (
    <>
      {isLoading ? (
        <Loading />
      ) : (
        <>
          {nowData && (
            <>
              <PageTitle title={"Home"} />
              <MainBanner imgUrl={nowData} />
              <Movies movieData={nowData} titleText={"  "} />
              <Movies movieData={upData} titleText={"  "} />
              <Movies movieData={popData} titleText={" "} />
              <Movies movieData={topData} titleText={" "} />
            </>
          )}
        </>
      )}
    </>
  );
};

